import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Global} from '../../../../Global';
import {Observable} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class EmailService {
  private baseUrl = this.global.baseUrl + '/api/admin/email/';
  private saveEmailUrl = this.baseUrl + 'save';
  private getAllUrl = this.baseUrl + 'get-all';
  constructor(private http: HttpClient, private global: Global) { }

  saveEmail(item): Observable<any> {
    return this.http.post<any>(this.saveEmailUrl, item);
  }

  getAll(): Observable<any> {
    return this.http.get<any>(this.getAllUrl);
  }
}
