import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {TicketService} from '../../../../../../business/service/ticket.service';
import {CategoryService} from '../../../../../../business/service/Admin/SystemConfig/category.service';
import {PriorityService} from '../../../../../../business/service/Admin/SystemConfig/priority.service';
import {EmailService} from '../../../../../../business/service/Admin/SystemConfig/email.service';

@Component({
  selector: 'app-new-email-setting',
  templateUrl: './new-email-setting.component.html',
  styleUrls: ['./new-email-setting.component.scss']
})
export class NewEmailSettingComponent implements OnInit {

  newEmailSettingForm: FormGroup;
  submitted = false;
  categoryList: any[];
  priorityList: any[];
  private categoryGroup: FormGroup;
  private priorityGroup: FormGroup;
  compareFn: ((f1: any, f2: any) => boolean) | null = this.compareByValue;

  constructor( private router: Router, private formBuilder: FormBuilder,
               private ticketService: TicketService,
               private categoryService: CategoryService,
               private priorityService: PriorityService,
               private emailService: EmailService) { }

  ngOnInit() {
    this.fetchPriorities();
    this.fetchCategories();
    this.createForm();
  }

  createForm() {
    this.newEmailSettingForm = this.formBuilder.group({
      host: ['', Validators.required],
      port: ['', Validators.required],
      accName: ['', Validators.required],
      email: ['', Validators.required],
      password: ['', Validators.required],
      emailAccountType: new FormControl()
    });
  }
  // convenience getter for easy access to form fields
  get f() { return this.newEmailSettingForm.controls; }

  onSubmit() {
    this.submitted = true;
    console.log(this.newEmailSettingForm.value);
    this.saveEmailSetting(this.newEmailSettingForm.value);
  }

  onReset() {
    this.submitted = false;
    this.newEmailSettingForm.reset();
  }

  private newTicket(value: any) {
    this.ticketService.saveTicket(value).subscribe(
      data => {
        console.log(data);
      },
      // tslint:disable-next-line:no-shadowed-variable
      error => {
      },
      () => {
        this.router.navigate(['/tickets']);
      }
    );
  }

  saveEmailSetting(item) {
    this.emailService.saveEmail(item).subscribe(
      data => {
        console.log(data);
      }
    );
  }

  fetchCategories() {
    this.categoryService.getAll().subscribe(
      data => {
        this.categoryList = data;
      }
    );
  }

  fetchPriorities() {
    this.priorityService.getAll().subscribe(
      data => {
        this.priorityList = data;
      }
    );
  }

  compareByValue(f1: any, f2: any) {
    return f1 && f2 && f1.id === f2.id;
  }

}
