import { Component, OnInit } from '@angular/core';
import {CategoryService} from '../../../../../../business/service/Admin/SystemConfig/category.service';
import {EmailService} from '../../../../../../business/service/Admin/SystemConfig/email.service';

@Component({
  selector: 'app-email-setting',
  templateUrl: './email-setting.component.html',
  styleUrls: ['./email-setting.component.scss']
})
export class EmailSettingComponent implements OnInit {

  rows: any[] = [];
  constructor(private categoryService: CategoryService, private emailService: EmailService) { }

  ngOnInit() {
    this.fetchEmailSetting();
  }

  fetchEmailSetting() {
    this.emailService.getAll().subscribe(
      data => {
        this.rows = data;
        console.log(data);
      }
    );
  }

}
