import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {SystemConfigDashboardComponent} from '../../SystemConfig/system-config/system-config-dashboard/system-config-dashboard.component';
import {CategoryComponent} from '../../SystemConfig/system-config/category/category.component';
import {NewCategoryComponent} from '../../SystemConfig/system-config/new-category/new-category.component';
import {PriorityComponent} from '../../SystemConfig/system-config/priority/priority.component';
import {NewPriorityComponent} from '../../SystemConfig/system-config/new-priority/new-priority.component';
import {StatusComponent} from '../../SystemConfig/system-config/status/status.component';
import {NewStatusComponent} from '../../SystemConfig/system-config/new-status/new-status.component';
import {EmailConfigDashboardComponent} from './email-config-dashboard/email-config-dashboard.component';
import {EmailSettingComponent} from './email-setting/email-setting.component';
import {EmailAccountsComponent} from './email-accounts/email-accounts.component';
import {EmailTypeComponent} from './email-type/email-type.component';
import {NewEmailSettingComponent} from './new-email-setting/new-email-setting.component';


const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Email Configuration'
    },
    children: [
      {
        path: '',
        redirectTo: 'email-config'
      },
      {
        path: '',
        component: EmailConfigDashboardComponent,
        data: {
          title: 'Email Configuration'
        },
      },
      {
        path: 'email-setting',
        component: EmailSettingComponent,
        data: {
          title: 'Email Setting'
        }
      },
      {
        path: 'new-email-setting',
        component: NewEmailSettingComponent,
        data: {
          title: 'New Email Setting'
        }
      },
      {
        path: 'email-account',
        component: EmailAccountsComponent,
        data: {
          title: 'Email Account'
        }
      },
      {
        path: 'email-type',
        component: EmailTypeComponent,
        data: {
          title: 'Email Type'
        }
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EmailConfigRoutingModule { }
